html {
  font-size: 1rem;
  box-sizing: border-box;
  height: 100%;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

:root {
  --body-bg:hsl(210, 46%, 5%);
  --bg:hsl(208, 31%, 10%);
  --color-white:#fff;
  --font-color:rgb(255, 185, 33);
  --app-bg:hsl(240, 8%, 3%);
  --font-primary:"Montserrat",sans-serif;
  --font-secondary:"Noto sans",sans-serif;
  --font-tertiary: "Concert One",sans-serif;
  --border-color:hsl(210, 100%, 40%);
}

body {
  background-color: var(--body-bg);
  height: 100%;
  font: 400 100%/160% var(--font-primary);
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 2rem 1rem;
  color: var(--font-color);
}

.app--container {
  text-align: center;
}
.app__header__container {
  margin-bottom: 1.5rem;
}
.app__header__container h1 {
  font-family: var(--font-tertiary);
  letter-spacing: 1px;
  font-size: 250%;
}
.app__header__container p {
  margin-top: 0.5rem;
  font-size: 0.85rem;
}
.app__body__container {
  background-color: var(--app-bg);
  display: grid;
  grid-template-areas: "search search" "temp temp" "sundata sundata" "days days";
  padding: 1.5rem;
  grid-gap: 1rem;
  justify-content: center;
  align-items: flex-start;
  box-shadow: 0 0 4px #000;
}

.city__name__input__wrapper,
.five__days__weather__data__section div,
.today__temp__section,
.today__sundata__section {
  background-color: var(--bg);
  border-radius: 7px;
  box-shadow: 0 0 2px var(--border-color);
}

/*****************************
INPUT FIELD
*******************************/
.city__name__input__wrapper {
  grid-area: search;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.city__name__input__wrapper input,
.city__name__input__wrapper button {
  border: none;
  background-color: var(--bg);
}
.city__name__input__wrapper input {
  width: 90%;
  border-radius: 7px 0 0 7px;
  font-size: 100%;
  font-family: var(--font-secondary);
  outline: none;
  color: var(--font-color);
  padding: 0.5rem;
}
.city__name__input__wrapper input:focus {
  box-shadow: 0 0 3px var(--border-color);
}
.city__name__input__wrapper input::placeholder {
  color: var(--font-color);
  opacity: 0.4;
}
.city__name__input__wrapper button {
  border-radius: 0 7px 7px 0;
}
.city__name__input__wrapper button i {
  font-size: 140%;
  padding: 0.5rem 0.65rem;
  color: var(--font-color);
  opacity: 0.5;
}
.city__name__input__wrapper button:hover i, .city__name__input__wrapper button:focus i {
  cursor: pointer;
  opacity: 1;
}

/**************************
TODAY TEMPERATURE SECTION
***************************/
.today__temp__section {
  grid-area: temp;
  padding: 1rem;
}
.today__temp__section img {
  height: 6rem;
  display: inline-block;
}
.today__temp__section h2 {
  font-size: 1.6rem;
}
.today__temp__section p {
  font-size: 0.9rem;
  letter-spacing: 0.5px;
}

.today__sundata__section {
  grid-area: sundata;
  padding: 0.5rem;
  height: 100%;
}
.today__sundata__section div {
  height: 50%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.today__sundata__section div img {
  height: 2rem;
  display: inline-block;
}
.today__sundata__section div p {
  font-size: 1rem;
}
.today__sundata__section div .sunrise__icon {
  margin-bottom: 0.35rem;
}

/*****************************
FIVE DAYS WEATHER DATA SECTION
*****************************/
.five__days__weather__data__section {
  grid-area: days;
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: 1fr;
}
.five__days__weather__data__section .day {
  padding: 0.4rem 1rem;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  font-family: var(--font-secondary);
}
.five__days__weather__data__section .day p {
  align-self: center;
  font-size: 1rem;
}
.five__days__weather__data__section .day img {
  height: 2.5rem;
  display: inline-block;
}
.five__days__weather__data__section .day h3 {
  font-size: 1rem;
  font-weight: 400;
}

/****************************
MEDIA QUERIES
****************************/
@media screen and (max-width: 480px) {
  html {
    font-size: 0.8rem;
  }
}
@media screen and (min-width: 481px) {
  html {
    font-size: 0.9rem;
  }

  body {
    align-items: center;
  }

  .today__temp__section {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .app__body__container {
    grid-template-areas: "search search" "temp sundata" "days days";
  }

  .five__days__weather__data__section {
    grid-template-columns: repeat(5, 1fr);
  }
  .five__days__weather__data__section .day {
    flex-direction: column;
    justify-content: center;
    align-items: Center;
  }
}
@media screen and (min-width: 769px) {
  html {
    font-size: 1rem;
  }
}
@media (min-width: 650px) and (max-width: 920px) and (orientation: landscape) {
  html {
    font-size: 0.8rem;
  }
}
@media screen and (min-width: 1280px) {
  html {
    font-size: 1.15rem;
  }
}